@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@300;400;500&display=swap");

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



body{
  font-family: 'IBM Plex Mono',sans-serif;
  background-color: #f7f7f7;
  margin-left: 350px;
  background-color:#f5ebe0 ;
}

h1{
  margin-bottom: 12px;
}

.card{
  width: 440px;
  margin: 40px;
  border: 4px solid black;
}

.card::after{
  content: "";
  display: block;
}

.avater{
  width: 100%;
  display: block;
}

.data{
  padding: 30px;
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
}

.skill_list{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 16px;
  margin-bottom: 5px;
  margin-left: 3px;
}

.skill{
  padding: 2px 12px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
}
